@media screen and (min-width: 1280px) {
    .regisImage{
        display: inline;
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0px;
        top: 0px;
        background: url(../../images/home.jpg);
        background-size: 100% 100%;
    }
    .frameRegis{
        display: inline;
        position: absolute;
        width: 380px;
        height: 480px;
        top: calc(50% - 240px);
        left: calc(50% - 190px);
        opacity: 0.9;
        background-color: #909497;
        border-radius: 10px;
    }
    .titleRegis{
        display: inline;
        position: absolute;
        width: 380px;
        height: 60px;
        top: calc(50% - 270px);
        left: calc(50% - 190px);
        color: #FFFFFF;
        font-size: 40px;
        font-weight: bold;
        text-align: center;
    }
    .txtStdName{
        position: absolute;
        width: 320px;
        height: 30px;
        top: calc(50% - 150px);
        left: calc(50% - 160px);
    }
    .txtStdPhone{
        position: absolute;
        width: 320px;
        height: 30px;
        top: calc(50% - 110px);
        left: calc(50% - 160px);
    }
    .txtStdPwd{
        position: absolute;
        width: 320px;
        height: 30px;
        top: calc(50% - 70px);
        left: calc(50% - 160px);
    }
    .txtStdRePwd{
        position: absolute;
        width: 320px;
        height: 30px;
        top: calc(50% - 30px);
        left: calc(50% - 160px);
    }
    .lblPrice{
        position: absolute;
        width: 320px;
        height: 30px;
        top: calc(50% - 10px);
        left: calc(50% - 150px);
        color: #FFFFFF;
        font-size: 20px;
        font-weight: 900;
        text-align: left;
    }
    .lblPayMethod{
        position: absolute;
        width: 320px;
        height: 30px;
        top: calc(50% + 60px);
        left: calc(50% - 150px);
        color: #FFFFFF;
        font-size: 16px;
        font-weight: 900;
        text-align: left;
        text-decoration: underline;
    }
    .btnRegis{
        display: inline;
        position: absolute;
        width: 180px;
        height: 35px;
        top: calc(50% + 185px);
        left: calc(50% - 90px);
        font-size: large;
        font-weight: 800;
    }
}
@media screen and (max-width: 1279px) {
    .frameRegis{
        display: inline;
        position: absolute;
        width: 96%;
        height: 96%;
        top: 2%;
        left: 2%;
        opacity: 0.9;
        background-color: #909497;
        border-radius: 10px;
    }
    .titleRegis{
        display: inline;
        position: absolute;
        width: 800px;
        height: 100px;
        top: calc(50% - 800px);
        left: calc(50% - 400px);
        color: #FFFFFF;
        font-size: 7rem;
        font-weight: bold;
        text-align: center;
    }
}