@media screen and (max-width: 1279px) {

}

@media screen and (min-width: 1280px) {
    .homeImage{
        display: inline;
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0px;
        top: 0px;
        background: url(../../images/home.jpg);
        background-size: 100% 100%;
    }
    .homeNav{
        display: inline;
        position: absolute;
        width: 100%;
        height: 55px;
        left: 0px;
        top: 0px;
        background-color: #F4F6F7;
        opacity: 0.85;
    }
    .homeLogo{
        display: inline;
        position: absolute;
        width: 40px;
        height: 40px;
        left: 25px;
        top: 7px;
        background: url(../../images/logo.jpg);
        background-size: 100% 100%;
        border-radius: 6px;
    }
    .homeLogin{
        display: inline;
        position: absolute;
        width: 100px;
        height: 40px;
        left: 65px;
        top: -5px;
        font-size: large;
        font-weight: bold;
        text-align: center;
        text-decoration: underline;
        color: #34495E;
    }
    .homeLogin:hover{
        cursor: pointer;
    }
    .contact{
        display: inline;
        position: absolute;
        width: 140px;
        height: 40px;
        left: calc(100% - 264px);
        top: -5px;
        font-size: large;
        font-weight: bold;
        text-align: center;
        color: #34495E;
    }
    .facebook{
        display: inline;
        position: absolute;
        width: 40px;
        height: 40px;
        left: calc(100% - 115px);
        top: 7px;
        background: url(../../images/fb.png);
        background-size: 100% 100%;
        border-radius: 6px;
    }
    .facebook:hover{
        cursor: pointer;
    }
    .whatsapp{
        display: inline;
        position: absolute;
        width: 40px;
        height: 40px;
        left: calc(100% - 65px);
        top: 7px;
        background: url(../../images/wa.png);
        background-size: 100% 100%;
        border-radius: 6px;
    }
    .whatsapp:hover{
        cursor: pointer;
    }
    /* src/components/Slideshow.css */
    .slideshow-container {
        position: absolute;
        max-width: 620px;
        height: 350px;
        left: calc(50% - 630px);
        top: calc(50% - 195px);
        margin: auto;
        overflow: hidden; /* Hide overflowing slides */
        border-radius: 6px;
        box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    }

    .slides-wrapper {
        display: flex; /* Arrange slides in a row */
        transition: transform 0.4s ease-in-out; /* Smooth slide transition */
    }

    .slide {
        min-width: 100%; /* Each slide takes full width of the container */
        box-sizing: border-box;
    }

    .slide img {
        width: 100%;
        height: auto;
        display: block; /* Remove extra space below image */
        border-radius: 8px;
    }
    .btnRegister{
        display: inline;
        position: absolute;
        overflow: clip;
        width: 200px;
        height: 40px;
        left: calc(50% - 430px);
        top: calc(50% + 180px);
        font-size: large;
        font-weight: bold;
    }
    .trialStudy{
        display: inline;
        position: relative;
        width: 620px;
        height: 478px;
        left: 0px;
        top: calc(50% - 268px);
        background-color: #000000;
        border: none;
        border-radius: 6px;
    }
    .trialTitle{
        display: inline;
        position: absolute;
        width: 602px;
        height: 32px;                                                                                                                                                                                                                                                           
        left: 0px;
        top: calc(50% - 270px);
        background-color: #000000;
        border: none;
        padding-left: 15px;
        padding-top: 8px;
        font-size: 20px;
        font-weight: 1000;
        color: #FFFFFF;
        border-top-left-radius: 6px;
        border-top-right-radius: 6px;
    }
}